.app {
  font-family: sans-serif;
  display: flex;
  flex-direction: row;
  margin-left: 30px;
  font-family: Roboto, Inter;
}

h1 {
  font-size: 70px;
  margin-top: 160px;
}

.text_p {
  font-size: 23px;
}

.side_svg {
  width: 500px;
  margin-right: 220px;
  margin-top: 200px;
}

.btn_group {
  display: flex;
}

.btn_2 {
  margin-left: 10px;
  font-size: 21px;
  padding: 9px;
}

.btn_1 {
  font-size: 20px;
  padding: 10px;
}

.footer {
  display: flex;
}

.footer1_img {
  margin-left: 10px;
  margin-right: 40px;
}

.footer1_1_img {
  margin-left: 10px;
}

.pep1 {
  position: absolute;
  top: 100px;
  left: 550px;
}

.pep1_img {
  margin-left: 40px;
}

.para1 {
  background-color: #ff8533;
  color: white;
  border-radius: 20px;
  padding: 5px 15px;
  margin-top: 2px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.pep2 {
  position: absolute;
  top: 70px;
  left: 1100px;
}

.para2 {
  background-color: #9966ff;
  color: white;
  border-radius: 20px;
  padding: 5px 15px;
  margin-top: 2px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.pep3 {
  position: absolute;
  top: 580px;
  left: 1200px;
}

.pep3_img {
  margin-left: 70px;
}

.para3 {
  background-color: #005ce6;
  color: white;
  border-radius: 20px;
  padding: 5px 15px;
  margin-top: 2px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
@media only screen and (max-width: 600px) {
  .app {
    display: flex;
    flex-direction: column;
    margin-left: 1px;
  }

  .side_svg {
    position: absolute;
    width: 270px;
    margin-left: 5px;
    margin-top: 100px;
  }
  h1 {
    font-size: 40px;
    text-align: center;
    margin-top: 10px;
  }

  .text_p {
    font-size: 20px;
    text-align: center;
    padding: 0px 3px;
  }
  .btn_group {
    display: flex;
    flex-direction: column;
  }

  .btn_2 {
    margin-left: 0px;
    font-size: 15px;
    padding: 10px;
  }

  .btn_1 {
    margin-bottom: 15px;
    font-size: 15px;
    padding: 10px;
  }

  .footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .footer1_img {
    margin-left: 0px;
    margin-right: 0px;
  }

  .pep1 {
    position: absolute;
    top: 520px;
    left: 190px;
  }

  .pep1_img {
    width: 40px;
  }

  .para1 {
    font-size: 9px;
  }

  .pep2 {
    position: absolute;
    top: 480px;
    left: 10px;
  }

  .pep2_img {
    width: 40px;
    margin-left: 80px;
  }

  .para2 {
    font-size: 9px;
  }
  .pep3 {
    position: absolute;
    top: 820px;
    left: 210px;
  }

  .pep3_img {
    width: 40px;
    margin-left: 40px;
  }

  .para3 {
    font-size: 9px;
  }
}
